.postPage {
    background-color: #f6f6f6;
    padding: 65px 0;
}

.postPage .container {
    border-radius: 15px;
    background-color: #fff;
    box-shadow: 0 0 40px -10px rgb(0 0 0 / 30%);
    padding: 0;
    overflow: hidden;
}

.postPage .container img {
    width: 100%;
}

.postInfo {
    padding: 45px;
    position: relative;
}

.postInfoLikes {
    position: absolute;
    bottom: 20px;
    right: 40px;
    cursor: pointer;
    user-select: none;
    font-size: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100px;
    height: 35px;
}

.postInfoLikes>.heart {
    width: 100px;
    height: 100px;
    background: url('https://cssanimation.rocks/images/posts/steps/heart.png') no-repeat;
    background-position: 0 0;
    cursor: pointer;
    transition: background-position 1s steps(28);
    transition-duration: 0s;
    transform: scale(0.7);
}

.postInfoLikes>.heart.is-active {
    transition-duration: 1s;
    background-position: -2800px 0;
}

.container.postInfoContainer {
    padding: 25px 45px;
    margin-top: 50px;
}

.container.postInfoContainer>form {
    display: flex;
    justify-content: space-between;
}

.postInfoContainer form input {
    width: -webkit-fill-available;
    border-radius: 5px;
    border: 1px solid #007acc;
    outline-style: none;
    padding: 5px 20px;
}

.postInfoContainer form button {
    background-color: transparent;
    border: none;
    margin-left: 20px;
    cursor: pointer;
}

.commentsContainer {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

.commentsContainer .comment {
    display: flex;
    margin-top: 50px;
}

.commentsContainer img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 30px;
}

.commentsContainer .commentInfo {
    width: fit-content;
    display: flex;
    justify-content: center;
    flex-direction: column;
    height: 100%;
    width: auto;
}