@charset "UTF-8";

.mainError {
	background-color: rgb(30, 29, 46);
	margin: 0 auto;
	height: 100vh;
	width: 100vw;
	position: fixed;
	top: 0;
	z-index: 100;
}

.mainError label {
	cursor: pointer;
}

.mainError a {
	margin: 0;
	padding: 0;
	vertical-align: baseline;
	background: transparent;
	text-decoration: none;
	color: #000;
}

.mainError .error {
	z-index: 100;
}

.mainError input,
select,
button,
textarea {
	margin: 0;
	font-size: 100%;
}

