.newPosts {
    background-color: #f6f6f6;
}

.newPosts .header__posts {
    display: flex;
}

.newPosts h1 {
    color: #333;
    font-size: 36px;
    font-weight: 700;
    padding: 50px 0 30px;
}

.newPosts .header__posts .courses_btn {
    margin-top: 40px;
    padding: 0.7rem 2rem;
    height: fit-content;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-family: "Poppins", sans-serif;
    font-weight: 700;
    font-size: 18px;
    text-align: center;
    text-decoration: none;
    color: rgb(0, 0, 0);
    backface-visibility: hidden;
    border: 0.3rem solid transparent;
    border-radius: 3rem;
    margin-left: 30px;
}

.newPosts .header__posts .csbtn {
    border-color: rgb(0, 0, 0);
    transition: transform 0.2s cubic-bezier(0.235, 0, 0.05, 0.95);
}

.header__posts .csbtn:hover {
    text-decoration: none;
    color: rgb(0, 0, 0);
    transform: perspective(1px) scale3d(1.044, 1.044, 1) translateZ(0) !important;
}

.newPosts section {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.newPosts section>a {
    width: 31%;
    margin-bottom: 30px;
    text-decoration: none;
}

.newPosts .container section article {
    border-radius: 5px;
    overflow: hidden;
    width: 100%;
    background-color: #fff;
    cursor: pointer;
    height: 100%;
}

.newPosts section article:hover .postImg img {
    transform: scale(1.1);
}

.newPosts section article:hover .postInfo h3 {
    color: #e8622d;
}

.newPosts .postImg {
    width: 100%;
    height: 125px;
    overflow: hidden;
}

.newPosts .postImg img {
    width: 100%;
    object-fit: cover;
    transition: transform 0.4s linear;
}

.newPosts .postInfo {
    padding: 20px 30px;
}

.newPosts .postInfo h3 {
    font-size: 20px;
    color: #333;
    font-weight: 500;
}

.newPosts .postInfo p {
    font-size: 18px;
    color: #7e7e7e;
}

@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:300,400);
* {
    margin: 0;
    padding: 0;
}

.newPosts .container {
    position: relative;
}

.newPosts .scrollDown {
    padding-top: 60px;
    position: absolute;
    bottom: 80px;
    right: -40px;
}

.newPosts .scrollDown span {
    position: absolute;
    width: 24px;
    height: 24px;
    border-left: 1px solid rgb(0, 0, 0);
    border-bottom: 1px solid rgb(0, 0, 0);
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
    -webkit-animation: sdb04 2s infinite;
    animation: sdb04 2s infinite;
    box-sizing: border-box;
    cursor: pointer;
}

@-webkit-keyframes sdb04 {
    0% {
        -webkit-transform: rotate(-45deg) translate(0, 0);
    }
    20% {
        -webkit-transform: rotate(-45deg) translate(-10px, 10px);
    }
    40% {
        -webkit-transform: rotate(-45deg) translate(0, 0);
    }
}

@keyframes sdb04 {
    0% {
        transform: rotate(-45deg) translate(0, 0);
    }
    20% {
        transform: rotate(-45deg) translate(-10px, 10px);
    }
    40% {
        transform: rotate(-45deg) translate(0, 0);
    }
}

@media (max-width:1025px) {
    .newPosts .postInfo {
        padding: 10px 15px;
    }

    .posts {
        padding-bottom: 110px;
    }

    .newPosts .scrollDown span {
		left: 3;
		right: 550px;
		top: 80px;
	}
}

@media (max-width:769px) {

    .newPosts .postInfo {
        padding: 10px 15px;
    }

    .newPosts section>a {
        width: 47%;
    }

    .newPosts .posts {
        display: flex;
        flex-wrap: wrap;
    }

    .newPosts .scrollDown span {
		left: 3;
		right: 410px;
		top: 70px;
	}

}

@media (max-width:426px) {

    .newPosts .postInfo {
        padding: 10px 15px;
    }

    .newPosts section>a {
        width: 99%;
    }

    .newPosts section>a:nth-child(n+4)  {
        display: none;
        padding-bottom: 20px;
    }

    .newPosts .posts {
        display: flex;
        flex-wrap: wrap;
    }

    .newPosts .scrollDown span {
		left: 3;
		right: 250px;
		top: 70px;
	}
}

@media (max-width: 376px) {

	.newPosts .scrollDown span {
		top: 70px;
		left: 3;
		right: 220px;
	}

    .newPosts .header__posts {
        display: block;
    }

    .newPosts .header__posts .csbtn {
        margin: 0;
        margin-bottom: 60px;
    }

}

@media (max-width: 321px) {

	.newPosts .scrollDown span  {
		top: 60px;
		left: 3;
		right: 190px;
	}

}