.allcomponents {
	display: flex;
	margin: 40px auto;
	height: 800px;
	width: 1200px;
	border-radius: 20px;
}

.allcomponents h1 {
	font-weight: bold;
	margin: 0;
}

.allcomponents h2 {
	text-align: center;
}

.allcomponents p {
	font-size: 14px;
	font-weight: 100;
	line-height: 20px;
	letter-spacing: 0.5px;
	margin: 20px 0 30px;
}

.allcomponents span {
	font-size: 12px;
}

.allcomponents a {
	color: #333;
	font-size: 14px;
	text-decoration: none;
	margin: 15px 0;
}

.allcomponents button {
	border-radius: 20px;
	border: 1px solid #12324d;
	background-color: #12324d;
	color: #ffffff;
	font-size: 12px;
	font-weight: bold;
	padding: 12px 45px;
	letter-spacing: 1px;
	text-transform: uppercase;
	transition: transform 80ms ease-in;
	cursor: pointer;
}

.allcomponents button:active {
	transform: scale(0.95);
}

.allcomponents button:focus {
	outline: none;
}

.allcomponents button.ghost {
	background-color: transparent;
	border-color: #ffffff;
}

.allcomponents form {
	background-color: #ffffff;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding: 0 50px;
	height: 100%;
	text-align: center;
}

.allcomponents input {
	background-color: #eee;
	border: none;
	padding: 12px 15px;
	margin: 8px 0;
	width: 100%;
	border-radius: 10px;
}

.allcomponents .container {
	background-color: #fff;
	border-radius: 10px;
	box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
	position: relative;
	overflow: hidden;
	width: 100%;
	max-width: 100%;
	min-height: 480px;
}

.allcomponents .form-container {
	position: absolute;
	top: 0;
	height: 100%;
	transition: all 0.6s ease-in-out;
}

.allcomponents .sign-in-container {
	left: 0;
	width: 50%;
	z-index: 2;
}

.allcomponents .container.right-panel-active .sign-in-container {
	transform: translateX(100%);
}

.allcomponents .sign-up-container {
	left: 0;
	width: 50%;
	opacity: 0;
	z-index: 1;
}

.allcomponents .container.right-panel-active .sign-up-container {
	transform: translateX(100%);
	opacity: 1;
	z-index: 5;
	animation: show 0.6s;
}

@keyframes show {
	0%,
	49.99% {
		opacity: 0;
		z-index: 1;
	}

	50%,
	100% {
		opacity: 1;
		z-index: 5;
	}
}

.allcomponents .overlay-container {
	position: absolute;
	top: 0;
	left: 50%;
	width: 50%;
	height: 100%;
	overflow: hidden;
	transition: transform 0.6s ease-in-out;
	z-index: 100;
}

.allcomponents .container.right-panel-active .overlay-container {
	transform: translateX(-100%);
}

.allcomponents .overlay {
	background: #12324d;
	background: -webkit-linear-gradient(to right, #2b80ff, #12324d);
	background: linear-gradient(to right, #2b80ff, #12324d);
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 0 0;
	color: #ffffff;
	position: relative;
	left: -100%;
	height: 100%;
	width: 200%;
	transform: translateX(0);
	transition: transform 0.6s ease-in-out;
}

.allcomponents .container.right-panel-active .overlay {
	transform: translateX(50%);
}

.allcomponents .overlay-panel {
	position: absolute;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding: 0 40px;
	text-align: center;
	top: 0;
	height: 100%;
	width: 50%;
	transform: translateX(0);
	transition: transform 0.6s ease-in-out;
}

.allcomponents .overlay-left {
	transform: translateX(-20%);
}

.allcomponents .container.right-panel-active .overlay-left {
	transform: translateX(0);
}

.allcomponents .overlay-right {
	right: 0;
	transform: translateX(0);
}

.allcomponents .container.right-panel-active .overlay-right {
	transform: translateX(20%);
}

.allcomponents .social-container {
	margin: 20px 0;
}

.allcomponents .social-container a {
	border: 1px solid #dddddd;
	border-radius: 50%;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	margin: 0 5px;
	height: 40px;
	width: 40px;
}

@media (max-width: 1025px) {
	.allcomponents .container {
		width: 80%;
		margin-left: 40px;
	}
}

@media (max-width: 769px) {
	.allcomponents {
		width: auto;
	}

	.allcomponents .container {
		margin-left: auto;
		margin-right: auto;
		width: 60%;
		height: 600px;
		margin-top: 100px;
	}

	.allcomponents .sign-in-container {
		width: 100%;
	}

	.allcomponents .overlay-container {
		display: none;
	}
}

@media (max-width: 426px) {
	.allcomponents {
		height: auto;
	}

	.allcomponents .container {
		width: 90%;
		height: 450px;
		margin-top: 0;
	}

	.allcomponents .sign-in-container {
		width: 100%;
	}
}
