@import url(https://fonts.googleapis.com/css?family=Gilda+Display);

.foundError {
	background-color: black;
	width: 100vw;
	height: 100vh;
	color: white;
	overflow: hidden;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	font-size: medium;
	position: fixed;
	top: 0;
	z-index: 1;
}

.foundError .error {
	text-align: center;
	font-family: 'Gilda Display', serif;

	text-align: center;
	width: 100%;
	height: 120px;
	margin: auto;
	position: absolute;
	top: 0;
	bottom: 0;
	left: -60px;
	right: 0;
	-webkit-animation: noise-3 1s linear infinite;
	animation: noise-3 1s linear infinite;
	overflow: default;
}

.foundError:after {
	content: 'error 404';
	font-family: OCR-A;
	font-size: 100px;

	text-align: center;
	width: 550px;
	margin: auto;
	position: absolute;
	top: 25%;
	bottom: 0;
	left: 0;
	right: 35%;
	opacity: 0;
	color: white;
	-webkit-animation: noise-1 0.2s linear infinite;
	animation: noise-1 0.2s linear infinite;
}
.foundError:before {
	content: 'error 404';
	font-family: OCR-A;
	font-size: 100px;

	text-align: center;
	width: 550px;
	margin: auto;
	position: absolute;
	top: 25%;
	bottom: 0;
	left: 0;
	right: 35%;
	opacity: 0;
	color: white;
	-webkit-animation: noise-2 0.2s linear infinite;
	animation: noise-2 0.2s linear infinite;
}

.foundError .info {
	text-align: center;
	width: 200px;
	height: 60px;
	margin: auto;
	position: absolute;
	top: 280px;
	bottom: 0;
	left: 20px;
	right: 0;
	-webkit-animation: noise-3 1s linear infinite;
	animation: noise-3 1s linear infinite;
}

.foundError .info:before {
	content: 'page not found';
	font-family: OCR-A;
	font-size: 100px;
	text-align: center;
	width: 800px;
	margin: auto;
	position: absolute;
	top: 20px;
	bottom: 0;
	left: 40px;
	right: 100px;
	opacity: 0;
	color: white;
	-webkit-animation: noise-2 0.2s linear infinite;
	animation: noise-2 0.2s linear infinite;
}

.foundError .info:after {
	content: 'page not found';
	font-family: OCR-A;
	font-size: 100px;
	text-align: center;
	width: 800px;
	margin: auto;
	position: absolute;
	top: 20px;
	bottom: 0;
	left: 40px;
	right: 0;
	opacity: 0;
	color: white;
	-webkit-animation: noise-1 0.2s linear infinite;
	animation: noise-1 0.2s linear infinite;
}

@-webkit-keyframes noise-1 {
	0%,
	20%,
	40%,
	60%,
	70%,
	90% {
		opacity: 0;
	}
	10% {
		opacity: 0.1;
	}
	50% {
		opacity: 0.5;
		left: -6px;
	}
	80% {
		opacity: 0.3;
	}
	100% {
		opacity: 0.6;
		left: 2px;
	}
}

@keyframes noise-1 {
	0%,
	20%,
	40%,
	60%,
	70%,
	90% {
		opacity: 0;
	}
	10% {
		opacity: 0.1;
	}
	50% {
