.foundError .wrap {
	top: 30%;
	left: 25%;

	height: 200px;

	margin-top: -100px;
	text-align: left;
	position: absolute;
	width: fit-content;
}
.foundError code {
	color: white;
}
.foundError span.blue {
	color: #48beef;
}
.foundError span.comment {
	color: #7f8c8d;
}
.foundError span.orange {
	color: #f39c12;
}
.foundError span.green {
	color: #33cc33;
}

.foundError .viewFull {
	font-family: OCR-A;
	color: orange;
}
@font-face {
	font-family: OCR-A;
	src: url('data:font/woff;base64,')
		format('woff');
}

@media only screen and (min-height: 500px) {
	.foundError .viewFull {
		display: none;
	}
}
