.header {
	background-color: #11324d;
}

.header-container {
	max-width: 1200px;
	padding: 20px;
	margin: 0 auto;
	display: flex;
	justify-content: space-between;
}

.header-container .logo {
	display: flex;
	align-items: center;
}

.logo a {
	cursor: pointer;
	color: #fff;
	text-decoration: none;
	font-size: 30px;
	font-weight: 400;
}

nav {
	width: 80%;
	margin: 0 auto;
	margin-left: 100px;
}

nav a:hover {
	text-decoration: none;
}

nav ul {
	list-style: none;
	text-align: right;
	padding: 0;
	margin-bottom: 0;
}

nav ul li {
	display: inline-block;
}

nav ul li a {
	display: block;
	padding: 10px;
	text-decoration: none;
	color: #fff;
	font-weight: 400;
	text-transform: uppercase;
	margin: 0 10px;
}

nav ul li a,
nav ul li a:after,
nav ul li a:before {
	transition: all 0.5s;
}

nav ul li a:hover {
	color: rgb(138, 138, 138);
}

nav.stroke ul li:last-child a {
	padding: 10px 0;
}

nav.stroke ul li:last-child a i {
	font-size: 23px;
}

nav.stroke ul li:last-child a img {
	border-radius: 50%;
	max-width: 30px;
	margin-bottom: 5px;
}

nav.stroke ul li a,
nav.fill ul li a {
	position: relative;
}

nav.stroke ul li a:after,
nav.fill ul li a:after {
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	margin: auto;
	width: 0%;
	content: '.';
	color: transparent;
	background: #aaa;
	height: 1px;
}

nav.stroke ul li a:hover:after {
	width: 100%;
}

nav.fill ul li a {
	transition: all 2s;
}

nav.fill ul li a:after {
	text-align: left;
	content: '.';
	margin: 0;
	opacity: 0;
}

nav.fill ul li a:hover {
	color: #fff;
	z-index: 1;
}

nav.fill ul li a:hover:after {
	z-index: -10;
	animation: fill 1s forwards;
	-webkit-animation: fill 1s forwards;
	-moz-animation: fill 1s forwards;
	opacity: 1;
}

.sticky {
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	z-index: 10;
	width: 100%;
}

.header .burger {
	background-color: transparent;
	border: none;
	cursor: pointer;
	padding: 0;
	outline: none;
	border: none;
	display: none;
}
.header .burger .line {
	fill: none;
	stroke: black;
	stroke-width: 6;
	transition: stroke-dasharray 600ms cubic-bezier(0.4, 0, 0.2, 1),
		stroke-dashoffset 600ms cubic-bezier(0.4, 0, 0.2, 1);
}
.header .burger .line1 {
	stroke-dasharray: 60 207;
	stroke-width: 6;
}
.header .burger .line2 {
	stroke-dasharray: 60 60;
	stroke-width: 6;
}
.header .burger .line3 {
	stroke-dasharray: 60 207;
	stroke-width: 6;
}
.header .burger.opened .line1 {
	stroke-dasharray: 90 207;
	stroke-dashoffset: -134;
	stroke-width: 6;
}
.header .burger.opened .line2 {
	stroke-dasharray: 1 60;
	stroke-dashoffset: -30;
	stroke-width: 6;
}
.header .burger.opened .line3 {
	stroke-dasharray: 90 207;
	stroke-dashoffset: -134;
	stroke-width: 6;
}

@media (max-width: 1025px) {
	nav.stroke ul li a {
		font-size: 14px;
	}

	nav.stroke ul li:last-child a img {
		max-width: 25px;
	}

	.header-container .logo {
		display: flex;
		align-items: center;
	}

	.logo a {
