	}
}

@media (max-width: 768px) {
	.header-container {
		padding: 7px 20px;
		position: relative;
	}

	.header-container .logo {
		height: 50px;
	}

	nav {
		margin-left: 0;
	}

	nav.stroke ul {
		display: flex;
		flex-direction: column;
		width: -webkit-fit-content;
		width: -moz-fit-content;
		width: fit-content;
		margin-left: auto;
		height: 0;
		transition: height 0.3s linear;
		overflow-y: hidden;
	}

	nav.stroke ul.showHeader {
		height: 214px;
	}

	nav.stroke ul li {
		width: -webkit-fit-content;
		width: -moz-fit-content;
		width: fit-content;
		margin-left: auto;
	}

	nav.stroke ul li a {
		padding-right: 0;
	}

	.header .burger {
		display: flex;
		margin-left: auto;
	}

	.header .burger svg {
		width: 45px;
		height: auto;
	}

	.header .burger .line {
		stroke: white;
	}
}

