.socials .rating {
	background-color: rgba(0, 0, 16, 0.8);
	padding: 0.4rem 0.4rem 0.1rem 0.4rem;
	border-radius: 2.2rem;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
}

.socials .rating-text p {
	color: rgba(0, 0, 16, 0.8);
	font-size: 1.3rem;
	padding: 0.3rem;
	width: 100%;
}

.socials svg {
	fill: rgb(242, 242, 242);
	height: 3.6rem;
	width: 3.6rem;
	margin: 0.2rem;
}

.socials .rating-form-2 svg {
	height: 3rem;
	width: 3rem;
	margin: 0.5rem;
}

.socials #radios label {
	position: relative;
}

.socials input[type="radio"] {
	position: absolute;
	opacity: 0;
}

.socials input[type="radio"] + svg {
	transition: all 0.2s;
}

.socials input + svg {
	cursor: pointer;
}

.socials input[class="super-happy"]:hover + svg,
.socials input[class="super-happy"]:checked + svg,
.socials input[class="super-happy"]:focus + svg {
	fill: rgb(0, 204, 79);
}

.socials input[class="happy"]:hover + svg,
.socials input[class="happy"]:checked + svg,
.socials input[class="happy"]:focus + svg {
	fill: rgb(0, 109, 217);
}

.socials input[class="neutral"]:hover + svg,
.socials input[class="neutral"]:checked + svg,
.socials input[class="neutral"]:focus + svg {
	fill: rgb(232, 214, 0);
}

.socials input[class="sad"]:hover + svg,
.socials input[class="sad"]:checked + svg,
.socials input[class="sad"]:focus + svg {
	fill: rgb(229, 132, 0);
}

.socials input[class="super-sad"]:hover + svg,
.socials input[class="super-sad"]:checked + svg,
.socials input[class="super-sad"]:focus + svg {
	fill: rgb(239, 42, 16);
}

