.socials .background body,
button,
input {
	font-family: "Montserrat", sans-serif;
	font-weight: 700;
	letter-spacing: 1.4px;
}

.socials .background {
	position: fixed;
	top: 50%;
	left: 50%;

	display: flex;
	min-height: 100vh;
	width: 100vw;
	z-index: 10;
	transition: -webkit-transform 0.3s linear;
	transition: transform 0.3s linear;
	transition: transform 0.3s linear, -webkit-transform 0.3s linear;
}

.socials .background .container {
	flex: 0 1 700px;
	margin: auto;
	padding: 10px;
}

.socials .background .screen {
	position: relative;
	background: rgb(10 10 39);
	border-radius: 15px;
}

.socials .background .screen:after {
	content: "";
	display: block;
	position: absolute;
	top: 0;
	left: 20px;
	right: 20px;
	bottom: 0;
	border-radius: 15px;
	box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4);
	z-index: -1;
}

.socials .background .screen-header {
	display: flex;
	align-items: center;
	padding: 10px 20px;
	background: #4d4d4f;
	border-top-left-radius: 15px;
	border-top-right-radius: 15px;
}

.socials .background .screen-header-left {
	margin-right: auto;
}

.socials .background .screen-header-button {
	display: inline-block;
	width: 8px;
	height: 8px;
	margin-right: 3px;
	border-radius: 8px;
	background: white;
}

.socials .background .screen-header-button.close {
	background: #ed1c6f;
	margin-top: 9.5px;
}

.socials .background .screen-header-button.maximize {
	background: #e8e925;
}

.socials .background .screen-header-button.minimize {
	background: #74c54f;
}

.socials .background .screen-header-right {
	display: flex;
}

.socials .background .screen-header-ellipsis {
	width: 3px;
	height: 3px;
	margin-left: 2px;
	border-radius: 8px;
	background: #999;
}

.socials .background .screen-body {
	display: flex;
}

.socials .background .screen-body-item {
	flex: 1 1;
	padding: 50px;
}

.socials .background .screen-body-item.left {
	display: flex;
	flex-direction: column;
}

.socials .background .app-title {
	display: flex;
	flex-direction: column;
	position: relative;
	color: #ea1d6f;
	font-size: 26px;
}

.socials .background .app-title:after {
	content: "";
	display: block;
	position: absolute;
	left: 0;
	bottom: -10px;
	width: 25px;
	height: 4px;
	background: #ea1d6f;
}

.socials .background .app-form-group {
	margin-bottom: 15px;
}

.socials .background .app-form-group.message {
	margin-top: 0;
}

.socials .background .app-form-group.buttons {
	margin-bottom: 0;
	text-align: right;
}

.socials .background .app-form-control {
	width: 100%;
	height: 200px;
	background: none;
	border: none;
	border-bottom: 1px solid #666;
	color: #ddd;
	font-size: 14px;
	text-transform: uppercase;
	outline: none;
	transition: border-color 0.2s;
}

.socials .background .app-form-control::-webkit-input-placeholder {
	color: #666;
}

.socials .background .app-form-control:-ms-input-placeholder {
	color: #666;
}

.socials .background .app-form-control::placeholder {
	color: #666;
}

.socials .background .app-form-control:focus {
	border-bottom-color: #ddd;
}

.socials .background textarea {
	resize: none;
}

.socials .background .app-form-button {
	background: none;
	border: none;
	color: #ea1d6f;
	font-size: 14px;
	cursor: pointer;
	outline: none;
	margin-left: 10px;
}

.socials .background .app-form-button:hover {
	color: #b9134f;
}

.socials .background .dribbble {
	width: 20px;
	height: 20px;
	margin: 0 5px;
}

