.foundError {
	background-color: black;
	width: 100vw;
	height: 100vh;
	color: white;
	overflow: hidden;
	-webkit-user-select: none;
	-ms-user-select: none;
	user-select: none;
	font-size: medium;
	position: fixed;
	top: 0;
	z-index: 1;
}

.foundError .error {
	text-align: center;
	font-family: 'Gilda Display', serif;

	text-align: center;
	width: 100%;
	height: 120px;
	margin: auto;
	position: absolute;
	top: 0;
	bottom: 0;
	left: -60px;
	right: 0;
	-webkit-animation: noise-3 1s linear infinite;
	animation: noise-3 1s linear infinite;
	overflow: default;
}

.foundError:after {
	content: 'error 404';
	font-family: OCR-A;
	font-size: 100px;

	text-align: center;
	width: 550px;
	margin: auto;
	position: absolute;
	top: 25%;
	bottom: 0;
	left: 0;
	right: 35%;
	opacity: 0;
	color: white;
	-webkit-animation: noise-1 0.2s linear infinite;
	animation: noise-1 0.2s linear infinite;
}
.foundError:before {
